.card-with-text {
    background-color: rgba(241, 240, 240, .65);
    height: 520px;
    width: 60vw;
    max-width: 600px;
    border-radius: 30px;
    overflow: hidden;
    padding: 5px 0 25px 20px;
    position: relative;
    z-index: 3;
}

.card-with-text__img-top {
    justify-self: left;
    width: 45%;
    margin: -40px 0 0 20px;
}

.card-with-text__img-bot {
    justify-self: right;
    width: 45%;
    margin: -100px 40px 0 0;
}

.card-with-text__list {
    height: 100%;
    width: 100%;
    border-radius: 0;
    overflow-y: scroll;
    list-style: none;
    padding: 0;
    border-bottom-style: solid;
    border-bottom-width: 0;
    display: grid;
    grid-template-columns: auto;
    gap: 40px;
    font-size: 16px;
    text-align: justify;
    text-transform: none;
}

/* СКРОЛЛБАР КАРТОЧКИ research */

.card-with-text__list_level_research {
    scrollbar-color: #A85B9F #E9E9E9;
    scrollbar-width: thin;
}

.card-with-text__list_level_research::-webkit-scrollbar {
    width: 15px;
}

.card-with-text__list_level_research::-webkit-scrollbar-track {
    background: #E9E9E9;
    border-radius: 15px;
}

.card-with-text__list_level_research::-webkit-scrollbar-thumb {
    background-color: #A85B9F;
    border-radius: 15px;
    padding-right: 30px;
    border: 3px solid #E9E9E9;
}

