@media (max-width: 1200px) {

    .card-with-text {
        width: 45vw;
    }
}

@media (max-width: 1100px) {

    .card-with-text__list {
        display: flex;
        flex-direction: column;
    }

    .card-with-text__img-bot {
        margin-top: -50px;
        align-self: flex-end;
    }
}


@media (max-width: 900px) {

    .card-with-text {
        width: 80vw;
        margin: 0 auto;
    }
}

@media (max-width: 500px) {


    .card-with-text {
        background-color: rgba(241, 240, 240, .9);
        height: 400px;
    }


